import React, { Component } from 'react';
import _ from 'lodash';
import socket from './socket';
import PeerConnection from './PeerConnection';
import MainWindow from './MainWindow';
import CallWindow from './CallWindow';
import CallModal from './CallModal';
import './CSS/app.scss';
import { PropTypes } from 'react'


class VideoCalling extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clientId: '',
      callWindow: '',
      callModal: '',
      callFrom: '',
      localSrc: null,
      peerSrc: null,
      clickImage:false,
      role:'',
      imgArray:null,
    };
    this.pc = {};
    this.config = null;
    this.startCallHandler = this.startCall.bind(this);
    this.endCallHandler = this.endCall.bind(this);
    this.rejectCallHandler = this.rejectCall.bind(this);
    this.setimagearray = this.setimagearray.bind(this);
  }
   

  componentDidMount() {
    //console.log(this.props.clickImage);
    this.setState({clickImage: this.props.clickImage});
    this.setState({role: this.props.role});
    console.log(this.props.role);
    socket
      .on('init', ({ id: clientId }) => {
        document.title = `${clientId} - VideoCall`;
        if (this.state.role ==='user')
        {
        this.setState({ clientId :'123'});
        console.log(this.state.clientId);
        }
        else{
          this.setState({ clientId });
          console.log("agent loggedin");
        }
      })
      .on('request', ({ from: callFrom }) => {
        this.setState({ callModal: 'active', callFrom });
      })
      .on('call', (data) => {
        if (data.sdp) {
          console.log(this.pc);
          console.log(data.sdp);
          this.pc.setRemoteDescription(data.sdp);
          if (data.sdp.type === 'offer') this.pc.createAnswer();
        } else this.pc.addIceCandidate(data.candidate);
      })
      .on('end', this.endCall.bind(this, false))
      .emit('init');
  }
  componentWillUnmount(){
    alert("Call Ended.");
  }
  componentDidUpdate(prevProps,prevState){
    console.log('prevProps',prevProps);
    console.log('this.props.clickImage',this.props.clickImage);
   // console.log(prevState); 
    if(prevProps.clickImage !== this.props.clickImage){
      console.log("state changed");
    this.setState({clickImage: this.props.clickImage});
  }
}
  setCapture(e){
    console.log(e);
  }
  setimagearray(params) {
    console.log("props",this.props);
    console.log(params);
  if(this.props.setimagearray)
  {  
    this.props.setimagearray(params);
  }
   //console.log(xyz);
     this.setState({imgArray : params});
    const arr= this.state.imgArray;
   console.log("params" , arr);
  } 
  
  startCall(isCaller, friendID, config) {
    this.config = config;
    console.log(isCaller , friendID,config);
    //console.log(this.props.clickImage);
    if(friendID == null){
      alert("Please enter Meeting ID");
      return false;
    }
    else{
    this.pc = new PeerConnection(friendID)
      .on('localStream', (src) => {
        const newState = { callWindow: 'active', localSrc: src };
        if (!isCaller) newState.callModal = '';
        this.setState(newState);
      })
      .on('peerStream', (src) => this.setState({ peerSrc: src }))
      .start(isCaller, config);
    }
  }

  rejectCall() {
    const { callFrom } = this.state;
    socket.emit('end', { to: callFrom });
    this.setState({ callModal: '' });
  }

  endCall(isStarter) {
    if (_.isFunction(this.pc.stop)) {
      this.pc.stop(isStarter);
    }
    this.pc = {};
    this.config = null;
    this.setState({
      callWindow: '',
      callModal: '',
      localSrc: null,
      peerSrc: null
    });
  }

  render() {
    const { clientId, callFrom, callModal, callWindow, localSrc, peerSrc,clickImage } = this.state;
    return (
      <div>
        { _.isEmpty(this.config) && this.state.role ==="agent"&& (
        <MainWindow
          clientId={clientId}
          startCall={this.startCallHandler}
        />)}
        {!_.isEmpty(this.config) && (
          <CallWindow
            status={callWindow}
            localSrc={localSrc}
            peerSrc={peerSrc}
            config={this.config}
            mediaDevice={this.pc.mediaDevice}
            endCall={this.endCallHandler}
            clickImage={clickImage}
            setimagearray={this.setimagearray}
            loginRole={this.props.role}
          />
        ) }
        <CallModal
          status={callModal}
          startCall={this.startCallHandler}
          rejectCall={this.rejectCallHandler}
          callFrom={callFrom}
        />
      </div>
    );
  }
}

export default VideoCalling;
