import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
//import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
//import ListItemText from '@material-ui/core/ListItemText';
//import ListItem from '@material-ui/core/ListItem';
//import List from '@material-ui/core/List';
//import Divider from '@material-ui/core/Divider';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
//import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
//import MyPhotoCamera from  './MyPhotoCamera';
import Rndex from './Rndex'
const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function CaptureDialogbox(props) {
  const classes = useStyles();
   const [open, setOpen] = React.useState(false);
   const [uploadfile,] = React.useState("");

   const handleClickOpen = () => {
    if(props.mandFill && props.Doctype===4)
    {
    setOpen(true);}
    else
    {
       alert("Document Type as Capture Image is missing");
       handleClose();}
  };

  const handleClose = () => {
    setOpen(false);
  };
  function handlefileChange(newValue) {
   console.log('arpit',newValue)
    props.onChange(newValue)
    handleClose()
  }

  return (
    <div>
      {/* <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Open full-screen dialog
      </Button> */}
      {/* <Button
        variant="contained"
        style={{background:'#367588',color:'white' ,width:350}}
        size="large"
        //className={classes.button}
        startIcon={<AddAPhotoIcon />}
       onClick={handleClickOpen}
      >
        Capture
      </Button> */}
      <label htmlFor="icon-button-file">
        <IconButton color="primary" aria-label="upload picture" component="span" onClick={handleClickOpen}>
          <PhotoCamera style={{ fontSize: 40,  marginTop:15,color:'#1a416a'}}  />
          <Typography variant="h6" className={classes.title}>
              Capture
            </Typography>
        </IconButton>
      </label>
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Capture
            </Typography>
            {/* <Button autoFocus color="inherit" onClick={handleClose}>
              Save
            </Button> */}
          </Toolbar>
        </AppBar>
        <Rndex uploadfile={uploadfile} onChange={handlefileChange}></Rndex>
      </Dialog>
    </div>
  );
}
