import React, { useState } from "react";
//import untitled from '../../images/Untitled.jpg'
//import { makeStyles, withStyles } from '@material-ui/core/styles';
import "./EquipmentCheck.css";
//import AppBar from '@material-ui/core/AppBar';
//import Toolbar from '@material-ui/core/Toolbar';
//import Typography from '@material-ui/core/Typography';
//import Button from '@material-ui/core/Button';
//import Logo from '../images/Logo.png';
//import moment from "moment";
import AspectRatio from "react-aspect-ratio";
import GridContainer from "../Grid/GridContainer";
//import MenuItem from '@material-ui/core/MenuItem';
import GridItem from "../Grid/GridItem";
//import { classNames } from 'classnames';
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import { orange, red, grey, green } from "@material-ui/core/colors";
import { IconButton } from "@material-ui/core";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";
import Webcam from "react-webcam";
import MicIcon from "@material-ui/icons/Mic";
import VideocamIcon from "@material-ui/icons/Videocam";
//import Divider from '@material-ui/core/Divider';
import VideocamOffIcon from "@material-ui/icons/VideocamOff";
import MicOffIcon from "@material-ui/icons/MicOff";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import SettingsIcon from "@material-ui/icons/Settings";
//import SettingsDrawer from '../SettingsDrawer';
import VPNdetection from "../VPNdetection";

//import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
//import Button from '@material-ui/core/Button';
//import List from '@material-ui/core/List';
//import Divider from '@material-ui/core/Divider';
//import ListItem from '@material-ui/core/ListItem';
//import ListItemIcon from '@material-ui/core/ListItemIcon';
//import ListItemText from '@material-ui/core/ListItemText';
//import InboxIcon from '@material-ui/icons/MoveToInbox';
//import MailIcon from '@material-ui/icons/Mail';
//import GridItem from '../components/Grid/GridItem';
//import GridContainer from '../components/Grid/GridContainer';
//import { makeStyles } from '@material-ui/core/styles';
import InputLabel from "@material-ui/core/InputLabel";
//import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
//import Toolbar from '@material-ui/core/Toolbar';
import UseGeoPosition from "../UseGeoposition/UseGeoposition";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    // width: '100%',
    color: "#3e485a",
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  drawer: {
    //width: drawerWidth,
    flexShrink: 0,
  },
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 450,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));
const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText(orange[500]),
    backgroundColor: orange[500],
    "&:hover": {
      backgroundColor: orange[700],
    },
    color: "white",
    fontFamily: "Poppins",
    marginTop: "15%",
  },
}))(Button);
const ColorIconButton = withStyles((theme) => ({
  root: {
    color: grey[700],
    //backgroundColor: orange[500],
    "&:hover": {
      color: red[700],
    },
    fontFamily: "Poppins",
  },
}))(IconButton);
const ColorMicButton = withStyles((theme) => ({
  root: {
    //color: red[700],
    backgroundColor: red[800],
    "&:hover": {
      backgroundColor: green[700],
    },
    fontFamily: "Poppins",
  },
}))(IconButton);
const ColorVideoButton = withStyles((theme) => ({
  root: {
    //color: red[700],
    backgroundColor: grey[100],
    "&:hover": {
      backgroundColor: green[700],
    },
    fontFamily: "Poppins",
  },
}))(IconButton);
function EquipmentCheckScreen() {
  const classes = useStyles();
  const [open, setopen] = useState(false);
  const [micenable, setmicenable] = useState(false);
  const [formValid, setFormValid] = useState(false);
  const [locOn, setLocOn] = useState(false);
  const [intSpeed, setIntSpeed] = useState(false);
  const [VPNdetect, setVPNdetect] = useState(false);
  const [audiodevices, setaudiodevices] = React.useState([]);
  const [videodevices, setvideodevices] = React.useState([]);
  const [speaker, setspeaker] = React.useState([]);
  // const [SettingOn, Settingoff] = React.useState([]);
  const [OpenSetting, setOpenSetting] = useState(true);
  //const [isReady, setIsReady] = useState(false);
  //const [role, setRole] = useState("user");
  const [latitude, setlatitude] = useState();
  const [longitude, setlongitude] = useState(); 
  const videoConstraints = {
    width: 2000,
    height: 1200,
    facingMode: "user",
  };
  var imageAddr = "/images/image.jpg" + "?n=" + Math.random();
  var startTime, endTime;
  var downloadSize = 200000;
  var download = new Image();

  startTime = new Date().getTime();
  download.src = imageAddr;

  let history = useHistory();

 

  function showResults() {
    var duration = (endTime - startTime) / 1000;
    var bitsLoaded = downloadSize * 8;
    var speedBps = Math.round(bitsLoaded / duration);
    var speedKbps = (speedBps / 1024).toFixed(2);
    // var speedMbps = (speedKbps / 1024).toFixed(2);
    if (parseInt(speedKbps, 10) >= 200) {
      setIntSpeed(true);
      setFormValid(micenable && open && locOn && true && VPNdetect);
      console.log("speedMbps", speedKbps);
    } else {
      setIntSpeed(false);
      setFormValid(false);
    }
  }
  //////Media Output///////

  const gotDevices = React.useCallback(
    (mediaDevices) =>
      setaudiodevices(
        mediaDevices.filter(({ kind }) => kind === "audioinput")
      ) |
      setvideodevices(
        mediaDevices.filter(({ kind }) => kind === "videoinput")
      ) |
      setspeaker(mediaDevices.filter(({ kind }) => kind === "audiooutput")),
    [setaudiodevices, setvideodevices, setspeaker]
  );
  //////Media Output///////
  console.log("setaudiodevices", audiodevices);
  console.log("setvideodevices", videodevices);
  console.log("setspeaker", speaker);

  React.useEffect(() => {
    endTime = new Date().getTime();
    !intSpeed && showResults();

    navigator.geolocation.getCurrentPosition(
      function (position) {
        setLocOn(true);
        setFormValid(micenable && open && true && intSpeed && VPNdetect);
        console.log("position", position);
        setlatitude(position.coords.latitude);
        setlongitude( position.coords.longitude);
      },
      function (error) {
        setLocOn(false);
        setFormValid(false);
      }
    );
    /////////////MeDIA OUTPUTS
    navigator.mediaDevices.enumerateDevices().then(gotDevices);
    ////////////
  }, [gotDevices]);

  function handleClose() {
    setopen(false);
    setFormValid(false);
  }

  function handlemic(e) {
    e.preventDefault();

    setmicenable(!micenable);
    //for form validation
    setFormValid(!micenable && open && locOn && intSpeed && VPNdetect);
  }
  function handlemicClose(e) {
    e.preventDefault();

    setmicenable(false);
    setFormValid(false);
  }

  function handleClick(e) {
    e.preventDefault();
    //alert('khole ya nahi');
    setopen(true);
    setFormValid(micenable && true && locOn && intSpeed && VPNdetect);
  }
  const setIPScore = (IPScore) =>
    setTimeout(() => {
      console.log("IPScore", IPScore);
      if (IPScore >= 0.99) {
        setVPNdetect(false);
        setFormValid(false);

        // console.log('speedMbps',speedKbps)
      } else {
        setVPNdetect(true);
        setFormValid(
          micenable && open && locOn && intSpeed && true && VPNdetect
        );
      }
    }, 2000);

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  function handleSetting(e) {
    e.preventDefault();
    //alert('khole ya nahi');
    setOpenSetting(!OpenSetting);
  }
  const handleChange = (event) => {
    const name = event.target.name;
    setState({
      ...state,
      [name]: event.target.value,
    });
  };
  
  const readyParams={lat: latitude, long: longitude, loginRole: 'user'};
  console.log(readyParams);
  const handleready = () => {
    history.push("/calling",readyParams );
  };

  /*  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  }; */

  return (
    <div>
      {/* <GridContainer> */}
      {/* <AppBar position="static" className={classes.appBar}>
        <Toolbar style={{ background: '#1a416a' }}>
          <Typography variant="h6" className={classes.title}>

          </Typography>
          <Button disabled style={{
            width: '163px', marginRight: '10px',
            height: '48px',
            opacity: 0.34,
            borderRadius: '40px',
            background: '#000000', color: '#FFFFFF'
          }}> {moment().format("DD-MM-YYYY hh:mm a")}</Button>
          <Button color="inherit">CONTACT US</Button>
          <Button color="inherit">HELP</Button>
          <Button color="inherit">FAQ</Button>
        </Toolbar>
      </AppBar> */}
      <div className="grey">
        <GridContainer xs={12} sm={12} md={12} className="check-equipment">
          <GridItem xs={12} sm={6} md={8}>
            <div className="Rectangle-77">
              <div className="equipment_screen">
                <GridItem xs={12} sm={6} md={12}>
                  <div className="div">
                    <h2 className="Heres-how-you-will-appear-in-the-meeting">
                      Here's how you will appear in the meeting.
                    </h2>
                  </div>
                </GridItem>

                <GridContainer>
                  {/* <div className='div2'> */}
                  <GridItem xs={2} sm={2} md={2}>
                    {/* <h3>Left Column 2</h3> */}
                  </GridItem>

                  <GridItem xs={8} sm={8} md={8}>
                    <div className="Exclusion-1">
                      <AspectRatio ratio="16/9">
                        {/* <h1>Left Column 8</h1> */}
                        {console.log("micenable", micenable)}
                        {open && (
                          <Webcam
                            mirrored="true"
                            audio={micenable}
                            onUserMedia="true"
                            videoConstraints={videoConstraints}
                            width={700}
                            height={300}
                            className="webcam"
                          ></Webcam>
                        )}
                      </AspectRatio>
                    </div>

                    <div className="Rectangle-9">
                      <GridContainer>
                        <GridItem xs={12} sm={6} md={6}>
                          {micenable ? (
                            <ColorMicButton onClick={handlemicClose}>
                              <MicIcon className="MicIcon" />
                            </ColorMicButton>
                          ) : (
                            <ColorMicButton onClick={handlemic}>
                              <MicOffIcon className="MicIcon" />
                            </ColorMicButton>
                          )}{" "}
                          <span className="mc-text">Enable Mic</span>
                        </GridItem>

                        {/* <GridItem xs={6} sm={6} md={2} >
                      <div ></div>
                      <p className='Line-1'></p>
                      </GridItem> */}
                        <GridItem xs={12} sm={6} md={6}>
                          {open ? (
                            <ColorVideoButton onClick={handleClose}>
                              <VideocamIcon className="VideocamIcon" />
                            </ColorVideoButton>
                          ) : (
                            <ColorVideoButton onClick={handleClick}>
                              <VideocamOffIcon className="VideocamIcon" />
                            </ColorVideoButton>
                          )}{" "}
                          <span className="mc-text">Enable Cam</span>
                        </GridItem>
                        <GridItem xs={6} sm={6} md={12}>
                          <div className="blankvideo-mic"></div>
                        </GridItem>
                      </GridContainer>
                      {/* <Divider orientation="vertical" /> */}
                    </div>
                  </GridItem>

                  <GridItem xs={2} sm={2} md={2}>
                    {/* <h3>Left Column 2</h3> */}
                  </GridItem>
                  {/* </div> */}
                </GridContainer>

                <GridItem xs={12} sm={6} md={12}>
                  <div className="div1g">
                    <div className="Make-sure-you-are-in-frame-and-under-bright-lightning-conditions">
                      Make sure you are in frame and under bright lightning
                      conditions
                    </div>

                    <div className="Rectangle-12">
                      <ColorButton
                        variant="contained"
                        disabled={!formValid}
                        color="primary"
                        className={classes.margin}
                        onClick={handleready}
                      >
                        OK, I'M Ready
                      </ColorButton>
                      {/* <p className='OK-IM-Ready'>OK, I'M Ready</p> */}
                    </div>
                  </div>
                </GridItem>
              </div>
            </div>
          </GridItem>

          <GridItem xs={12} sm={6} md={4}>
            {OpenSetting ? (
              <div>
                <div className="Ins-mainheading">
                  Instructions
                  <ColorIconButton>
                    <SettingsIcon fontSize="large" onClick={handleSetting} />
                  </ColorIconButton>
                </div>

                <div className="Instruction-lines">
                  <p className="Ins-heading">
                    Please make sure your device microphone is ON{" "}
                    {micenable && (
                      <IconButton>
                        <CheckCircleIcon style={{ color: "green" }} />
                      </IconButton>
                    )}{" "}
                  </p>
                  <p className="Ins-subheading">
                    Microphone will be needed for communication on the other
                    side of the conference.
                  </p>

                  <p className="Ins-heading">
                    Please make sure your camera is ON
                    {open && (
                      <IconButton>
                        <CheckCircleIcon style={{ color: "green" }} />
                      </IconButton>
                    )}
                  </p>
                  <p className="Ins-subheading">
                    Sit facing your camera in a well lit area. Your face should
                    be visible with in the frame.
                  </p>

                  <p className="Ins-heading">
                    Please make sure your location is turned ON
                    {locOn && (
                      <IconButton>
                        <CheckCircleIcon style={{ color: "green" }} />
                      </IconButton>
                    )}
                  </p>
                  <p className="Ins-subheading">
                    We need to collect your geolocation data as per Video KYC
                    guidelines.
                  </p>

                  <p className="Ins-heading">
                    Please make sure you have good internet
                    {intSpeed && (
                      <IconButton>
                        <CheckCircleIcon style={{ color: "green" }} />
                      </IconButton>
                    )}
                  </p>
                  <p className="Ins-subheading">
                    We require a minimum of 200Kbps to perform video KYC.
                  </p>

                  <p className="Ins-heading">
                    {" "}
                    Please make sure you are not using any VPN
                    {VPNdetect && (
                      <IconButton>
                        <CheckCircleIcon style={{ color: "green" }} />
                      </IconButton>
                    )}
                  </p>
                  <p className="Ins-subheading">
                    The use of VPN/Proxy server is not in compliance with video
                    KYC guidelines.
                  </p>
                  <VPNdetection parentCallback={setIPScore} />
                  <div>{/* <UseGeoPosition/> */}</div>
                </div>
              </div>
            ) : (
              <div>
                <div className="Ins-mainheading">
                  Settings
                  <ColorIconButton>
                    <HighlightOffIcon
                      fontSize="large"
                      onClick={handleSetting}
                    />
                  </ColorIconButton>
                </div>
                <div className="setting-device">
                  <FormControl
                    variant="outlined"
                    className={classes.formControl}
                  >
                    <InputLabel htmlFor="outlined-age-native-simple">
                      Camera
                    </InputLabel>
                    <Select
                      native
                      // value={key}
                      onChange={handleChange}
                      label="Camera"
                      inputProps={{
                        name: "Camera",
                        id: "outlined-age-native-simple",
                      }}
                    >
                      {videodevices.map((device, key) => (
                        <option value={key}>{device.label}</option>
                      ))}
                    </Select>
                  </FormControl>
                </div>
                <div className="setting-device">
                  <FormControl
                    variant="outlined"
                    className={classes.formControl}
                  >
                    <InputLabel htmlFor="outlined-age-native-simple">
                      Microphone
                    </InputLabel>
                    <Select
                      native
                      //value={key}
                      onChange={handleChange}
                      label="Microphone"
                      inputProps={{
                        name: "Microphone",
                        id: "outlined-age-native-simple",
                      }}
                    >
                      {audiodevices.map((device, key) => (
                        <option value={key}>{device.label}</option>
                      ))}
                    </Select>
                  </FormControl>
                </div>
                <div className="setting-device">
                  <FormControl
                    variant="outlined"
                    className={classes.formControl}
                  >
                    <InputLabel htmlFor="outlined-age-native-simple">
                      Speaker
                    </InputLabel>
                    <Select
                      native
                      // value={key}
                      // key={key}
                      onChange={handleChange}
                      label="Speaker"
                      inputProps={{
                        name: "Speaker",
                        id: "outlined-age-native-simple",
                      }}
                    >
                      {speaker.map((device, key) => (
                        <option value={key} key={key}>
                          {device.label}
                        </option>
                      ))}
                    </Select>
                  </FormControl>
                </div>
              </div>
            )}
            {/*////////////////////// SETTINGS ////////////////////////*/}
          </GridItem>
        </GridContainer>
      </div>
    </div>
  );
}

export default EquipmentCheckScreen;
