import React,{useState,useEffect} from 'react'
import axios from 'axios'
import './Geolocation.css'


 

 function UseGeoposition() {

  
     
   const [latitude,setlatitude]=useState('');
   const [longitude,setlongitude]=useState('');
   const [address,setaddress]=useState('');
  useEffect(() => {
    var options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 100
    };
    function error(err) {
      console.warn(`ERROR(${err.code}): ${err.message}`);
    }
    navigator.geolocation.getCurrentPosition(
      function (position) {
        //console.log("position latitude :", position.coords.latitude);
        //console.log("position longitude :", position.coords.longitude);
        //console.log("position acc :", position.coords.accuracy);
        // axios.get('https://cors-anywhere.herokuapp.com/http://apis.mapmyindia.com/advancedmaps/v1/r71dil56vyofb5zdqnwfispcjaigktln/rev_geocode?lat=28.626362991&lng=77.371759945')
        let Key="549a1d7a-53c5-4d19-a927-45c51fc75619"
        if (latitude.length === 0 || longitude.length === 0) {
          setlatitude(position.coords.latitude);
          setlongitude(position.coords.longitude);
        }
        //console.log(latitude, longitude);
        axios
          .get(
            "https://cors-anywhere.herokuapp.com/http://apis.mapmyindia.com/advancedmaps/v1/r71dil56vyofb5zdqnwfispcjaigktln/rev_geocode?lat="+
              latitude +
              "&lng=" +
              longitude
          )
          //.get('https://cors-anywhere.herokuapp.com/http://products.avonmobility.com:88/api/Gpsmap/Geocoder?latitude='+latitude+'&longitude='+longitude+'&apikey='+Key)
         // .get( 'https://cors-anywhere.herokuapp.com/http://products.avonmobility.com:88/api/Gpsmap/Geocoder?latitude=27.569860&longitude=80.678670&apikey='+Key)
          .then((res) => {
            if (res) {
              //console.log("res", res);
              //console.log("res", res.data.Address);
              const resultAdd =
                /* res.data.results[0].street +
                "," + */
                res.data.results[0].locality +
                "," +
                res.data.results[0].city +
                "," +
                res.data.results[0].district +
                "," +
                res.data.results[0].area;
              //console.log(resultAdd);
              //setaddress(res.data.results[0].formatted_address);
              setaddress(resultAdd);
            } else {
              console.log("ERROR 404");
            }
          })
          .catch((error) => {
            console.log(error);
            setaddress("India");
          });
      },
      error,
      options
    );

    console.log("Only once",address);
  });

  return (
         <>
             <span className='address'>{address}</span>
         </>
     )
 }
 
 export default UseGeoposition
 
