import React, { useState } from 'react';
import GalleryImage from "./GalleryImage";

function GalleryFrame( props){
    const setInd= (index, indChkVal) => {
       
        props.setIndex(index,indChkVal )
    };
{console.log("imgChkValGalFrame",props.imgChkVal)}
   
    return(
        <div className = 'ImgFrame' >
                     <GalleryImage setIndex={setInd} imgChkVal = {props.imgChkVal}  imageArray={props.imageArray}/> 
        </div>
    )
}

export default GalleryFrame