import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import { useSpring, animated } from 'react-spring/web.cjs'; // web.cjs is required for IE 11 support
import Button from '@material-ui/core/Button';
import GridItem from "../components/Grid/GridItem.js";
import GridContainer from "../components/Grid/GridContainer.js";
import SnackbarContent from "../components/Snackbar/SnackbarContent.js";
import CardHeader from "../components/Card/CardHeader.js";
import Card from "../components/Card/Card";
const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

const Fade = React.forwardRef(function Fade(props, ref) {
  const { in: open, children, onEnter, onExited, ...other } = props;
  const style = useSpring({
    from: { opacity: 0 },
    to: { opacity: open ? 1 : 0 },
    onStart: () => {
      if (open && onEnter) {
        onEnter();
      }
    },
    onRest: () => {
      if (!open && onExited) {
        onExited();
      }
    },
  });

  return (
    <animated.div ref={ref} style={style} {...other}>
      {children}
    </animated.div>
  );
});

Fade.propTypes = {
  children: PropTypes.element,
  in: PropTypes.bool.isRequired,
  onEnter: PropTypes.func,
  onExited: PropTypes.func,
};

export default function InstructionModal() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
        <Button href="#text-buttons" color="primary" onClick={handleOpen} >
               View Instructions
        </Button>
      
      <Modal
        aria-labelledby="spring-modal-title"
        aria-describedby="spring-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        {/* <Fade in={open}>
          <div className={classes.paper}> */}
          <GridContainer>
       <GridItem xs={12} sm={12} md={12}>
          <Card>
          <CardHeader color='sirius'><font size='5'>Upload Instructions</font></CardHeader>
            {/* <h2 id="spring-modal-title">Upload Instructions</h2> */}
            {/* <p id="spring-modal-description">react-spring animates me.</p> */}
            <br></br>
            <GridItem xs={12} sm={12} md={12}>
                         <SnackbarContent
                           message={
                           "1.Please select the document type from the drop down list"
                                  }
                           //close
                         //icon={LinkedCameraTwoToneIcon}
                         color='aliceblue'
                        />
                        </GridItem>
                        <GridItem xs={12} sm={12} md={12}>
                         <SnackbarContent
                           message={
                           "2.Enter document no in the test box "
                                  }
                           //close
                         //icon={LinkedCameraTwoToneIcon}
                         color='aliceblue'
                        />
                        </GridItem>
                         <GridItem xs={12} sm={12} md={12}>
                         <SnackbarContent
                           message={
                           "3.Click Browse to upload from local Disk or"
                                  }
                           //close
                         //icon={LinkedCameraTwoToneIcon}
                         color='aliceblue'
                        />
                        </GridItem>
                        <GridItem xs={12} sm={12} md={12}>
                         <SnackbarContent
                           message={
                           "4.Click Capture to use camera/ scanner"
                                  }
                           //close
                         //icon={LinkedCameraTwoToneIcon}
                         color='aliceblue'
                        />
                        </GridItem>
                        
                        </Card>
                        </GridItem>
                        </GridContainer>
          {/* </div>
        </Fade> */}
      </Modal>
    </div>
  );
}
