import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
// import Check from '@material-ui/icons/Check';
// import SettingsIcon from '@material-ui/icons/Settings';
// import GroupAddIcon from '@material-ui/icons/GroupAdd';
// import VideoLabelIcon from '@material-ui/icons/VideoLabel';
// import StepConnector from '@material-ui/core/StepConnector';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import UploadDocumentScreen from './UploadDocumentScreen'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
//import Typography from '@material-ui/core/Typography';
//import Button from '@material-ui/core/Button';
import Logo from '../images/Logo.png';
import moment from "moment";
import EquipmentCheckScreen from './EquipmentCheckScreen/EquipmentCheckScreen';
import './Style.css';
import InstructionScreen from './InstructionScreen';
//import UseGeoPosition from'./UseGeoposition/UseGeoposition'
import { Redirect} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    
  },
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

function getSteps() {
  return ['Document Upload', 'Instructions', 'Video Kyc'];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return <UploadDocumentScreen/>;
    case 1:
      return <InstructionScreen/>;
    case 2:
      return <EquipmentCheckScreen/>;
    default:
      return 'KYC COMPLETED';
  }
}

function getHeader(step) {
  switch (step) {
    case 0:
      return "Upload Document Screen";
    case 1:
      return "Instructions";
    case 2:
      return 'Video KYC';
    default:
      return 'KYC Completed';
  }
}

export default function Steppers(props) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();
  const token = localStorage.getItem("token");
  const [isLoggedIn, setIsLoggedIn] = React.useState(token ? true : false);
  const [isLoggedOut, setIsLoggedOut] = React.useState(false);
  console.log(props.location.state);
  const handleNext = (e) => {
    
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  const handleLogout = () => {
    console.log("logout clicked")
    localStorage.removeItem("token");
    setIsLoggedOut(true);
  };
  return (
    <div className={classes.root}>
      {!isLoggedIn ? (
        <Redirect to="/" />
      ) : (
        <div>
          {/* <container> */}
          <AppBar position="static">
            <Toolbar style={{ background: "#1a416a" }}>
              {/* <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton> */}
              <img src={Logo} alt="" className="logo"></img>
              <Typography variant="h6" className={classes.title}></Typography>
              {/* <UseGeoPosition/> */}
              <Button
                disabled
                style={{
                  width: "163px",
                  marginRight: "10px",
                  height: "48px",
                  opacity: 0.34,
                  borderRadius: "40px",
                  background: "#000000",
                  color: "#FFFFFF",
                }}
              >
                {moment().format("DD-MM-YYYY hh:mm a")}
              </Button>
              <Button color="inherit">CONTACT US</Button>
              <Button color="inherit">HELP</Button>
              <Button color="inherit">FAQ</Button>
              <Button color="inherit" onClick={handleLogout}>
                LOGOUT
              </Button>
            </Toolbar>
          </AppBar>

          <h1 className="main-heading">
            {activeStep === 0 ? (
              ""
            ) : (
              <Button
                disabled={activeStep === 0}
                onClick={handleBack}
                className="back"
              >
                Back
              </Button>
            )}
            {getHeader(activeStep)}

            <div>
              {activeStep === steps.length ? (
                <div>
                  <Typography className={classes.instructions}>
                    Thank You
                  </Typography>
                  {/* <Button onClick={handleReset} className={classes.button} style={{ background: '#1a416a',color:'white'}}>
              Reset
            </Button> */}
                </div>
              ) : (
                //   <div>

                <Button
                  className=" next"
                  variant="contained"
                  // color="primary"

                  onClick={handleNext}
                  // className={classes.button}
                >
                  {activeStep === steps.length - 1 ? "Finish" : "Next"}
                </Button>

                //   </div>
              )}
            </div>
          </h1>

          <Stepper
            alternativeLabel
            activeStep={activeStep}
            style={{ padding: 0 }}
          >
            {steps.map((label) => (
              // <h1 className="main-heading">Upload Documents</h1>
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <div>
            {activeStep !== steps.length ? (
              <div>
                <Typography className={classes.instructions}>
                  {getStepContent(activeStep)}
                </Typography>
              </div>
            ) : (
              ""
            )}
          </div>
          {/* </containker> */}
          {isLoggedOut ? props.history.push('/login'): ""}
        </div>
      )}
    </div>
  );
}
