import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Logo from "../images/Logo.png";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import moment from "moment";
import { Redirect, useHistory } from "react-router-dom";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";
import CallIcon from "@material-ui/icons/Call";
import { SelectionState, RowDetailState } from "@devexpress/dx-react-grid";
import {
  Grid,
  Table,
  TableHeaderRow,
  TableSelection,
} from "@devexpress/dx-react-grid-material-ui";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  button: {
    marginRight: theme.spacing(1),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function Steppers(props) {
  const classes = useStyles();
  const token = localStorage.getItem("token");
  const [isLoggedIn, setIsLoggedIn] = React.useState(token ? true : false);
  const [isLoggedOut, setIsLoggedOut] = React.useState(false);
  const [selection, setSelection] = useState();
  const history = useHistory();
  const columns = [
    { name: "reqId", title: "Request No." },
    { name: "customer", title: "Customer Name" },
    { name: "status", title: "Status" },
  ];
  const rows = [
    { reqId: 123, customer: "ABC", status: "Pending" },
    { reqId: 456, customer: "XYZ", status: "Done" },
  ];

  const onRowSelect = (e, reqID) => {
    setSelection(e);
    console.log("setSelection",reqID);
    history.push("/calling?id="+reqID);
  };

  const handleLogout = () => {
    console.log("logout clicked");
    localStorage.removeItem("token");
    setIsLoggedOut(true);
  };
  return (
    <div className={classes.root}>
      {!isLoggedIn ? (
        <Redirect to="/" />
      ) : (
        <div>
          <AppBar position="static">
            <Toolbar style={{ background: "#1a416a" }}>
              <img src={Logo} alt="" className="logo"></img>
              <Typography variant="h6" className={classes.title}></Typography>
              <Button
                disabled
                style={{
                  width: "163px",
                  marginRight: "10px",
                  height: "48px",
                  opacity: 0.34,
                  borderRadius: "40px",
                  background: "#000000",
                  color: "#FFFFFF",
                }}
              >
                {moment().format("DD-MM-YYYY hh:mm a")}
              </Button>
              <Button color="inherit">CONTACT US</Button>
              <Button color="inherit">HELP</Button>
              <Button color="inherit">FAQ</Button>
              <Button color="inherit" onClick={handleLogout}>
                LOGOUT
              </Button>
            </Toolbar>
          </AppBar>
          <Paper>
            <Grid rows={rows} columns={columns}>
              <SelectionState
                selection={selection}
                onSelectionChange={(e) => onRowSelect(e,rows.reqId)}
              />
              <Table />
              <TableHeaderRow />
              <TableSelection
                selectByRowClick
                highlightRow
                showSelectionColumn={true}
              />
            </Grid>
          </Paper>
          {isLoggedOut ? props.history.push("/login") : ""}
        </div>
      )}
    </div>
  );
}
