import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
//import VideoRecord from "../images/record.svg";
//import IconButton from "@material-ui/core/IconButton";
import Avatar from "@material-ui/core/Avatar";
import RecordOn from "../../src/RecordOn.svg";
import RecordOff from "../../src/RecordOff.svg";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { useTheme } from "@material-ui/core/styles";
import axios from "axios";

const defaultDisplayMediaOptions = {
  audio: true,
  video: { cursor: "always" },
};

const useStyles = makeStyles((theme) => ({
  root: {
    "& .MuiListItem-gutters": {
      paddingRight: "0%",
    },
  },
  recorder: {
    width: "0px",
    //height: "24px",
    //paddingLeft : "10px",
  },
  avatar: {
    width: "25px",
    height: "auto",
    marginRight: "6px",
  },
}));

function useScreenRecording({
  displayMediaOptions = defaultDisplayMediaOptions,
  onEnd = () => {},
  onError = () => {},
  onStart = () => {},
} = {}) {
  const [captureStream, setCaptureStream] = useState(null);
  const [error, setError] = useState(null);
  const [mediaRecorder, setMediaRecorder] = useState(null);
  const [isRecording, setIsRecording] = useState(false);
  const [recording, setRecording] = useState(null);

  const stopRecording = () => {
    try {
      setIsRecording(false);
      mediaRecorder.stop();
      captureStream.getTracks().forEach((track) => track.stop());
    } catch (e) {
      onError(e);
      setError(e);
    }
  };
  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getDisplayMedia(
        displayMediaOptions
      );
      setIsRecording(true);
      stream.getTracks().forEach((track) => {
        track.onended = stopRecording;
      });
      setCaptureStream(stream);
      const recorder = new MediaRecorder(stream);
      recorder.ondataavailable = (event) => {
        onEnd(event);
        setRecording(event.data);
      };
      recorder.start();
      setMediaRecorder(recorder);
      onStart({ stream, recorder });
    } catch (e) {
      setIsRecording(false);
      onError(e);
      setError(e);
    }
  };

  const toggleRecording = () =>
    isRecording ? stopRecording() : startRecording();
  return {
    captureStream,
    error,
    isRecording,
    mediaRecorder,
    recording,
    startRecording,
    stopRecording,
    toggleRecording,
  };
}
export default function ScreenRecording() {
  const classes = useStyles();
  const [dialogOpen, setDialogOpen] = useState(false);
  const [recorder, setrecorder] = useState();
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));

  const handleClose = () => {
    setDialogOpen(false);
  };
  // this function is used to generate random file name
  const getFileName = (fileExtension) => {
    var d = new Date();
    var year = d.getUTCFullYear();
    var month = d.getUTCMonth();
    var date = d.getUTCDate();
    return (
      "RecordRTC-" +
      year +
      month +
      date +
      "-" +
      getRandomString() +
      "." +
      fileExtension
    );
  };

  const getRandomString = () => {
    if (
      window.crypto &&
      window.crypto.getRandomValues &&
      navigator.userAgent.indexOf("Safari") === -1
    ) {
      var a = window.crypto.getRandomValues(new Uint32Array(3)),
        token = "";
      for (var i = 0, l = a.length; i < l; i++) {
        token += a[i].toString(36);
      }
      return token;
    } else {
      return (Math.random() * new Date().getTime())
        .toString(36)
        .replace(/\./g, "");
    }
  };

  const handleUpload = () => {
    if (recorder) {
      // get recorded blob
      console.log(recorder);

      // generating a random file name
      var fileName = getFileName("webm");

      // we need to upload "File" --- not "Blob"
      var fileObject = new File([recorder], fileName, {
        type: "video/webm",
      });

      const formData = new FormData();

      // recorded data
      formData.append("file", fileObject);

      // file name
      formData.append("title", fileObject.name);
      formData.append("requestNumber",'5f8d6887a5d350251fc0d491');

      formData.forEach(element => {
        console.log(element);
      });

      axios
        .post("http://18.191.100.2:9091/kycvideoservice/savevideo", formData)
        .then((res) => {
          //if (res.data === "OK") {
            alert("Video Uploaded Successfully");
            setrecorder("");
            setDialogOpen(false);
         // }
        })
        .catch((error) => {
          console.log(error);
          alert("ERROR !! Please try again. ");
        });
    }
  };
  const { isRecording, recording, toggleRecording } = useScreenRecording();
  //const setrecord = (recordData, isrecording) => {
  //    console.log(recordData, isrecording);
  useEffect(() => {
    if (isRecording === false && recording) {
      setrecorder(recording);
      setDialogOpen(true);
    }
  }, [isRecording, recording]);
  //parentCallback(recording, isRecording);
  return (
    <div>
      <Button
        onClick={toggleRecording}
        //color="primary"
        //variant="outlined"
        // style={{display:"none"}}
        className={classes.recorder}
        startIcon={
          isRecording ? (
            <Avatar src={RecordOn} className={classes.avatar} />
          ) : (
            <Avatar src={RecordOff} className={classes.avatar} />
          )
        }
      >
        {/* {isRecording ? "Stop" : "Start"}  */}
      </Button>
      {/*  {!!recording && (
        <video autoPlay src={recording && URL.createObjectURL(recording)} />
      )} */}

      <Dialog
        fullScreen={fullScreen}
        open={dialogOpen}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
        disableEscapeKeyDown={true}
        disableBackdropClick={true}
      >
        <DialogTitle id="responsive-dialog-title">
          {"View Recoreded Call"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            Do You want to Upload this Recording?
          </DialogContentText>
          {!!recorder && (
            <video
              autoPlay
              src={recorder && URL.createObjectURL(recorder)}
              style={{ width: "90%" }}
            />
          )}
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleUpload} color="primary">
            Upload
          </Button>
          <Button onClick={handleClose} color="primary" autoFocus>
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
