import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";

import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
//import Typography from "@material-ui/core/Typography";
//import Divider from "@material-ui/core/Divider";
import KYCData from "../KYCDetails.json";
import GridItem from "./Grid/GridItem";
import GridContainer from "./Grid/GridContainer";
import Button from "@material-ui/core/Button";
//import Icon from '@material-ui/core/Icon';
// import SaveIcon from '@material-ui/icons/Save';
// import ArrowLeft from '@material-ui/icons/ArrowLeft';
// import ArrowRight from '@material-ui/icons/ArrowRight';
import axios from "axios";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import "./KYCDetails.css";
import Fly from "../images/Fly.png";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
//import Poppins from '../fonts/Poppins-Regular.ttf';
//import Poppins from 'https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap';

const useStyles = makeStyles((theme) => ({
  root: {
    "&.makeStyles-grid-22": {
      padding: "30px 5px !important",
    },
  },
  kycContent: {
    width: "213px",
    height: "51px",
    fontFamily: "Poppins",
    fontSize: "36px",
    fontWeight: "bold",
    fontStretch: "normal",
    fontStyle: "normal",
    lineHeight: "1.39",
    letterSpacing: "normal",
    textAlign: "center",
    color: "#1a416a",
  },
}));

export default function KYCDetails(props) {
  const classes = useStyles();
  const [documents, setDocuments] = useState(0);
  const [verData, setVerData] = useState(KYCData);
  //const [verData, setVerData] = useState("");

  //const [verData, setVerData] = useState(...KYCData,{'requestNumber': "5f244a5623578a7646e4b894"})
  const [prevDis, setPrevDis] = useState(true);
  const [nexDis, setNexDis] = useState(true);
 // const [temChk, setTemChk] = useState(false); //for check box
  const [radioVal, setRadioVal] = useState("0");
  const [queIndex, setQueIndex] = useState(0);
  const [trvChk, setTrvChk] = useState(false); //for submit validation
  const [temp, setTemp] = useState(0);
  //5f45178340a52902398bec37
  useEffect(() => {
    axios
      .get(
        "http://18.191.100.2:9092/kyccustomerservice/questionAndAnswer/5f8d6887a5d350251fc0d491"
        //"http://localhost:8090/kyccustomerservice/questionAndAnswer/5f8a3cb9c45d5e77d24bb983"
      )
      .then((res) => {
        console.log("res", res);
        setTemp(1);
        setVerData(res.data);
        // alert('Data Saved Sucessfully')
      })
      .catch((error) => {
        console.log(error);
        alert("Error 404");
      });
  }, [temp]); 

  const handleNext = (e, str) => {
    e.preventDefault();
    let qIndex = queIndex + 1;
    setPrevDis(false);
    props.parentCallback(verData[documents - 1].question[qIndex]);
    //setRadioVal("")
    // if (!verData[documents - 1].matched[qIndex]) {
    // if (!verData[documents - 1].verified[qIndex]) {
    //   const newData = verData;
    //   newData[documents - 1].verified.push(false);
    //   newData[documents - 1].matched.push("");
    //  newData[documents - 1].traverse[qIndex] = true;
    //  setTemChk(false);
    //   setRadioVal("")
    //   setVerData(newData);
    //   setNexDis(true);//disabling till used set match or not match
    // } else {
      //setTemChk(verData[documents - 1].verified[qIndex]);
      setRadioVal(verData[documents - 1].matched[qIndex])
    //}
   // if (!verData[documents - 1].question[qIndex + 1]) {
    if (verData[documents - 1].matched[qIndex]) {
      setNexDis(false);
    }
    else
    { setNexDis(true);}
    if (!verData[documents - 1].question[qIndex + 1]) {
      setNexDis(true);
    }
    setQueIndex(qIndex);
  };

  const handlePrev = (e, str) => {
    setNexDis(false);
    let qIndex = queIndex - 1;
    setQueIndex(qIndex);
   props.parentCallback(verData[documents - 1].question[qIndex]);

    if (!verData[documents - 1].question[qIndex - 1]) {
      setPrevDis(true);
    }
    //setTemChk(verData[documents - 1].verified[qIndex]);
    setRadioVal(verData[documents - 1].matched[qIndex]);
  };
  // const handleClick = (e, chkVal) => {
  
  //   setTemChk(!verData[documents - 1].verified[queIndex]);
  //   verData[documents - 1].verified[queIndex] = !verData[documents - 1].verified[queIndex];
  //   setVerData(verData);
  // };
  const handleRadClick = (e, chkVal) => {
    setRadioVal(chkVal)
     
     verData[documents - 1].matched[queIndex] = chkVal      
     setVerData(verData);
     if (verData[documents - 1].question[queIndex+1]) {
        //if not the last que, enable the next button, else do not enable it.
        setNexDis(false);//as user set any match or not match...next button will be enabled.
        
     }
        else
        {
          //check for button, once we reach at last que of selected doc
      setTrvChk(() => {
        for (var j = 0; j < verData.length; j++) {
          for (var i = 0; i < verData[j].question.length; i++) {
            //if (!verData[j].traverse) {
              console.log("match",verData[j].matched)
              if(!verData[j].matched)
              {
                return false
              }
              else if (verData[j].matched[i] != "match" && verData[j].matched[i] != "notMatch") {
                  return false;
                }
             }
          }
       // }
        return true;
      });
     }

   };
  const handleSubmit = () => {
    axios
      .post("http://18.191.100.2:9092/kycquestionservice/saveQuestionAndAnswers", verData)
    //.post("http://localhost:8090/kycquestionservice/saveQuestionAndAnswers", verData)
      .then((res) => {
        console.log("res", res);

        alert("Data Saved Sucessfully");
        document.getElementById("onSubmit").style.display='none';
        props.setGalDis(false)
      })

      .catch((error) => {
        console.log(error);
        alert("Error 404");
        document.getElementById("onSubmit").style.display='none';
        props.setGalDis(false)
      });
  };
  const handleButtons = (e, str) => {
    setQueIndex(0);

    if (str !== 0) {
      setPrevDis(true);
      setNexDis(true);//by default disable, until select any option fom match or not match

      if (!verData[str - 1].requestNumber) {
        const newData = verData;
        newData[str - 1].requestNumber = "5f8d6887a5d350251fc0d491";
        //newData[str-1].requestNumber='5f45184b40a52902398bec39';
        setVerData(newData);
        //console.log('ver',verData)
      }

      props.parentCallback(verData[str - 1].question[0]);
      // if (!verData[str - 1].traverse) {
      //   const newTrans = verData;
      //   var traverse = Array(verData[str - 1].question.length);
      //   newTrans[str - 1] = { ...newTrans[str - 1], traverse };
      //   traverse[0] = true;
      //   for (var i = 1; i < traverse.length; i++) {
      //     traverse[i] = false;
      //   }
      //   setVerData(newTrans);
      // }

      // if (!verData[str - 1].verified) {
      //   const newData = verData;
      //   newData[str - 1] = { ...newData[str - 1], verified: [false] }; //default one entry
      //   setVerData(newData);
      //   setTemChk(false);
      // } else {
      //   setTemChk(verData[str - 1].verified[0]);
      // }
      if (!verData[str - 1].matched) {
        const newData = verData;
        var matched = Array(verData[str - 1].question.length);
        newData[str - 1] = { ...newData[str - 1], matched };
        matched[0] = "";
        for (var i = 1; i < matched.length; i++) {
          matched[i] = "";
        }
        //newData[str - 1] = { ...newData[str - 1], matched: [0] }; //default one entry
        setVerData(newData);
        setRadioVal("");
      } else {
        setRadioVal(verData[str - 1].matched[0]);
        setNexDis(false);//as for this document, matched and not match already set..so enabling next button
      }
    } else {
      setPrevDis(true);
      setNexDis(true);
      document.getElementById("onSubmit").disabled = true;
    }
    setDocuments(str);
  };

  const queAnsDiv = (index, qIndex) => {
    return (
      <div style={{ display: "flex" }}>
        {/* <h4> Verified: </h4> */}
        {console.log("verData", verData)}
        {/* <FormControlLabel
          control={
            <Checkbox
              className="Rectangle-40"
              checked={temChk}
              onChange={(e, str) => handleClick(e, e.target.value)}
            />
          }
        /> */}
       
        {/* <h4> Answer: </h4> */}
        <p className="Answer"> {verData[index - 1].answer[qIndex]} </p>
        <FormControl component="fieldset">
  {/* <FormLabel component="legend"></FormLabel> */}
  <br/>
  <br/>
  <RadioGroup aria-label="Matched" name="match" value={radioVal} 
  onChange={(e, str) => handleRadClick(e, e.target.value)} style={{display:'block'}}>
    <FormControlLabel value="match" control={<Radio />} label="Matched" />
    <FormControlLabel value="notMatch" control={<Radio />} label="Not Matched" />
  </RadioGroup>
  
</FormControl>
      </div>
    );
  };

  const onSubmit = (event) => {
    console.log(event);
  };
  return (
    <div>
      <GridContainer class="detail-container">
  
        <GridItem xs={12} sm={12} md={12} >
          <p className="KYC-Details">KYC Details</p>
        <FormControl variant="outlined" className={classes.formControl}>
        <div className="my-dd"> <InputLabel id="demo-simple-select-outlined-label">
              Documents
            </InputLabel>
            <Select
              // style = {{width: 200}}
            /*  style={{
                width: 299,
                height: "54px",
                backgroundColor: "#ffffff",
                borderRadius: "5px",
                border: "1px #b2b2b2",
                textAlign: "left",
              }}*/
              
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              value={documents}
              onChange={(e, str) => handleButtons(e, e.target.value)}
              label="documents"
            >
              <MenuItem disabled value="0">
                <em> None </em>
              </MenuItem>
              {verData.map((data, key) => (
                <MenuItem value={key + 1} key={key + 1}>
                  {" "}
                  {data.documentType}
                </MenuItem>
              ))}
              {/* {verData?verData.map((data,key) => (
                    <MenuItem value={key+1}> {data.documentType}</MenuItem>
                )):""} */}
            </Select>
            <div className="my-selection">
            {documents && documents !== "0"
              ? queAnsDiv(documents, queIndex)
              : ""}
          </div>
          </div></FormControl>

         
          

          <GridContainer>
            <GridItem xs={12} sm={6} md={6}>
              <Button
                variant="contained"
                color="primary"
                size="small"
                className="Back"
                id="openPrevQues"
                disabled={prevDis}
                onClick={handlePrev}
              >
                BACK
              </Button>
            </GridItem>
            <GridItem xs={12} sm={6} md={6}>
              <Button
                variant="contained"
                color="primary"
                size="small"
                className="Next"
                id="openNextQues"
                disabled={nexDis}
                onClick={handleNext}
              >
                NEXT
              </Button>
            </GridItem>
          </GridContainer>
          <GridContainer>
            <GridItem>
              <Button
                variant="contained"
                color="primary"
                size="small"
                className="Submit"
                // startIcon={<SaveIcon />}
                disabled={!trvChk}
                type="submit"
                id="onSubmit"
                onClick={handleSubmit}
              >
                SUBMIT<img src={Fly} className="send" alt="Submit"></img>
              </Button>
            </GridItem>
          </GridContainer>
        </GridItem>
      </GridContainer>
    </div>
  );
}
