import React from 'react';
//import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
//import DialogContentText from '@material-ui/core/DialogContentText';
//import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
//import useMediaQuery from '@material-ui/core/useMediaQuery';
//import { useTheme } from '@material-ui/core/styles';
import PublishIcon from '@material-ui/icons/Publish';
import MyDropzone from "../components/MyDropzone";
import Typography from '@material-ui/core/Typography';
export default function UploadDialogbox(props) {
  const [open, setOpen] = React.useState(false);
  const [uploadfile, setUploadfile] = React.useState("");
  //const theme = useTheme();
  //const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const handleClickOpen = () => {
    if(props.uploadedfile)
    {
    if(props.mandFill)
    {
    setOpen(true);}
    else
    {
       alert("Document Type or Number is missing");
       handleClose();}
    }
    else
    {
       alert("Please Capture the image");
       handleClose();}

  };

  const handleClose = () => {
    setOpen(false);
  };
  function handlefileChange(newValue) {
    setUploadfile(newValue);
    props.onChange(newValue)
    handleClose()
  }
  return (
    <div>
        <label htmlFor="icon-button-file">
        <IconButton color="primary"  onClick={handleClickOpen}  >
          <PublishIcon style={{ fontSize: 40 ,  marginTop:15,color:'#1a416a'}} />
          <Typography variant="h6" >
              Upload
            </Typography>
        </IconButton>

      </label>
      {/* <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Open responsive dialog
      </Button> */}
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        {/* <DialogTitle id="responsive-dialog-title">{"Use Google's location service?"}</DialogTitle> */}
        <DialogContent>
       < MyDropzone uploadfile={uploadfile} onChange={handlefileChange}></MyDropzone>
          {/* <DialogContentText>
            Let Google help apps determine location. This means sending anonymous location data to
            Google, even when no apps are running.
          </DialogContentText> */}
        </DialogContent>
        <DialogActions>
          {/* <Button autoFocus onClick={handleClose} color="primary">
            Disagree
          </Button> */}
          {/* <Button onClick={handleClose} color="primary" autoFocus>
            Close
          </Button> */}
        </DialogActions>
      </Dialog>
    </div>
  );
}
