import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import RoomIcon from "@material-ui/icons/Room";
import MenuIcon from "@material-ui/icons/Menu";
import IconButton from "@material-ui/core/IconButton";
import GridItem from "./Grid/GridItem";
import Grid from "@material-ui/core/Grid";
import GridContainer from "./Grid/GridContainer";
import { Offline, Online, Detector } from "react-detect-offline";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Switch from "@material-ui/core/Switch";
import UseGeoposition from './UseGeoposition/UseGeoposition';
import MiniLogo from "../images/Mini_Logo.png";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { Redirect } from "react-router-dom";
// import './CallingScreen.css';
const useStyles = makeStyles((theme) => ({
  root: {

    "& .MuiAppBar-root": {
      flexDirection: "row",
    },
    "& .MuiToolbar-regular": {
      minHeight: "2px",
    },
    "&.MuiAppBar-positionFixed": {
      marginLeft: "60px",
    },
    display: "flex",
  },
  appBar: {
    [theme.breakpoints.up("sm")]: {

      width: "95%",
      backgroundColor: "#ffffff",
      height: "8.7%",

    },
  },
  // location: {
  //     width: "316px",
  //     fontFamily: "Poppins",
  //     fontSize: "18px",
  //     fontWeight: "300",
  //     fontStretch: "normal",
  //     fontStyle: "normal",
  //     lineHeight: "2.78",
  //     letterSpacing: "normal",
  //     textAlign: "center",
  //     color: "#1a416a",
  //   }
}));
const AntSwitch = withStyles((theme) => ({
  root: {
    width: 28,
    height: 16,
    padding: 0,
    display: "flex",
  },
  switchBase: {
    padding: 2,
    color: theme.palette.grey[500],
    "&$checked": {
      transform: "translateX(12px)",
      color: theme.palette.common.white,
      "& + $track": {
        opacity: 1,
        backgroundColor: theme.palette.primary.main,
        borderColor: theme.palette.primary.main,
      },
    },
  },
  thumb: {
    width: 12,
    height: 12,
    boxShadow: "none",
  },
  track: {
    border: `1px solid ${theme.palette.grey[500]}`,
    borderRadius: 16 / 2,
    opacity: 1,
    backgroundColor: theme.palette.common.white,
  },
  checked: {},
}))(Switch);

function Callinglayouttoolbar(props) {
  const classes = useStyles();
  const token = localStorage.getItem("token");
  const [isLoggedIn, setIsLoggedIn] = React.useState(token ? true : false);
  const [isLoggedOut, setIsLoggedOut] = React.useState(false);
  // console.log('props',props)
  const handleLogout = () => {
    localStorage.removeItem("token");
    setIsLoggedOut(true);
  };
  const geoParams = {
    latitude: props.layoutparam.latitude[0],
    longitude: props.layoutparam.longitude[0],
  };
  console.log(geoParams);
  return (
    <div className="my-row">
      {!isLoggedIn ? (
        <Redirect to="/" />
      ) : (
          <div>
            <GridContainer>
              <GridItem xs={12} sm={12} md={12}>
                <AppBar
                  elevation={0}
                  position="static"
                  className={classes.appBar}
                >
                  <Toolbar >
                    {/* <Toolbar style={{paddingLeft:70}} > */}
                    {/* <GridItem xs={4} sm={4} md={1}>
          <IconButton>
            <MenuIcon style={{ fill: "#1a416a" }} />
          </IconButton>
          
          </GridItem> */}
                    <div className="logo1">
                      <img src={MiniLogo} alt="logo" className={classes.logo} />
                    </div>
                    <RoomIcon class="my-icon" style={{ fill: "#1a416a" }} />
                    <GridItem md={6}>
              <Typography noWrap className={classes.location}>

                        <UseGeoposition geoParams={geoParams} />
                      </Typography>
                    </GridItem>
                    <GridItem md={5}>
                      <Typography component="div" className={classes.location}>
                        <div className="my-status"><Grid component="label" container alignItems="center" spacing={1}>
                          <Grid item>
                            <Offline>Offline</Offline>
                          </Grid>
                          <Grid item>
                            <Detector
                              render={({ online }) => (
                                <div>
                                  <AntSwitch
                                    checked={online ? true : false}
                                    name="checkStatus"
                                  />
                                </div>
                              )}
                            />
                          </Grid>
                          <Grid>
                            <Online>Online</Online>
                          </Grid>
                        </Grid>
                        </div>
                      </Typography>
                    </GridItem>

                    <GridItem md={1}>
                      <IconButton onClick={handleLogout}>
                        <ExitToAppIcon style={{ fill: "#1a416a" }} />
                      </IconButton>
                    </GridItem>
                  </Toolbar>
                </AppBar>
              </GridItem>
            </GridContainer>
            {isLoggedOut ? <Redirect to="/" /> : ""}
          </div>
        )
      }
    </div >
  );
}

export default Callinglayouttoolbar
