import React, { useState, useEffect } from 'react';
//import ReactDOM from 'react-dom';

import './WaitingScreen.css';
import GridItem from "../../components/Grid/GridItem.js";
import GridContainer from "../../components/Grid/GridContainer.js";

import image1 from '../../images/testimony.png';
import image2 from '../../images/Untitled-1.png';
import image3 from '../../images/phone.png';
import VideoCalling from "../VideoCall/VideoCalling";

export default function WaitingScreen (props) {
//const imageUrl  = image2;
const [seconds, setSeconds] = useState(10);
const [callvideo, setcallvideo]= useState(false);

    //   function reset() {
    //     setSeconds(600);
    //   }
    
      useEffect(() => {
        let interval = null;
        if (seconds > 0) {
          interval = setInterval(() => {
            setSeconds(seconds => seconds - 1);
          }, 1000);
        } else if (seconds === 0) {
          if(props.role === "user"){
            setcallvideo(true);
          }
          //reset();
          //alert("Please call after sometime.")
        }
        return () => clearInterval(interval);
      }, [seconds])

        return(
        // <React.Fragment>
            <div className="back-img">
               {callvideo == true? <VideoCalling role="user" />:  <><GridContainer>
               
                <GridItem xs={12} sm={12} md={12}> 
                <h1 className = "Font-style">A representative has been assigned to you. </h1>
                </GridItem> 
                <GridItem xs={12} sm={12} md={12}> 
                <img src = {image1} alt = "Defaultimage" className="Screen-info"/>
                </GridItem> 
                <GridItem xs={12} sm={12} md={12}> 
                    <p className = "Font-style1">Please wait while representative pickup your call <br></br> 
                    Expect wait time is around <b className = "Text-style"> {seconds} </b> seconds</p>
                </GridItem> 
                <GridItem xs={12} sm={12} md={12}> 
                    <img src = {image3} alt = "Defaultimage" className="Phone"/>
                </GridItem> 
                </GridContainer> </>}
            </div>    
        // </React.Fragment>
        )
    }


