import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
//import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import VideocamIcon from "@material-ui/icons/Videocam";
import CameraAltIcon from "@material-ui/icons/CameraAlt";
import ScheduleIcon from "@material-ui/icons/Schedule";
import IconButton from "@material-ui/core/IconButton";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import MiniLogo from "../images/Mini_Logo.png";
import Divider from "@material-ui/core/Divider";
import GalleryIcon from "@material-ui/icons/Collections";
import ScreenRecording from "./ScreenRecording";
import camera from "../sounds/camera_shutter.mp3";
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 0,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
  // items: {
  //   border: "1px solid red",
  // },
  drawer: {
    width: "auto",
  },
}));

export default function CommonDrawer(props) {
  const cameraAudio = new Audio(camera);
  const playSound = audioFile => {
    audioFile.play();
  };
  const role = props.loginRole;
  console.log(role);
  const classes = useStyles();
  //const [galRequired, setGalRequired] = useState(false);
  //const [vidRequired, setVidRequired] = useState(false);
  const [gallerydisabled, setgallerydisabled] = useState(props.galDis);
  const [clickevent,setClickEvent]=useState(1);
  const opengallery = (param) => {
    if (param === "g") {
      //setGalRequired(!galRequired);
      props.galreqd(true);
      //console.log(galRequired);
    } else {
      //setVidRequired(!vidRequired);
      props.vidreqd(true);
      //console.log(vidRequired);
    }
  };
  const clickImage=(arg)=>{
    console.log('arg',arg)
    if(arg==="yes"){
      playSound(cameraAudio)
      setClickEvent(clickevent+1);
    }
    console.log('clickevent',clickevent);
    props.captureReqd(clickevent);
  }
  useEffect(() => {
    setgallerydisabled(props.galDis);
  }, [props.galDis]);

  return (
    <div className={classes.root}>
      <Grid container>
        <Drawer variant="permanent" className={classes.drawer}>
          <div>
            <List>
              {/* <ListItem>
              <img src={MiniLogo} alt="logo" className={classes.logo} />
            </ListItem> */}
             {/* <Divider />*/}
              <ListItem>
                <IconButton className={classes.paper}>
                  <ScheduleIcon style={{ fill: "#1a416a" }} />
                </IconButton>
              </ListItem>
              <ListItem>
                {role === "user" ? (
                  <IconButton
                    className={classes.paper}
                    disabled
                    color="primary"
                  >
                    <VideocamIcon />
                  </IconButton>
                ) : (
                  <IconButton
                    className={classes.paper}
                    onClick={() => opengallery("v")}
                  >
                    <VideocamIcon style={{ fill: "#1a416a" }} />
                  </IconButton>
                )}
              </ListItem>
              <ListItem>
                {role === "user" ? (
                  <IconButton className={classes.paper} disabled>
                    <CameraAltIcon />
                  </IconButton>
                ) : (
                  <IconButton className={classes.paper} onClick={()=>clickImage("yes")}>
                    <CameraAltIcon style={{ fill: "#1a416a" }} />
                  </IconButton>
                )}
              </ListItem>
              <ListItem>
                {role === "user" ? (
                  <IconButton className={classes.paper} disabled>
                    <GalleryIcon />
                  </IconButton>
                ) : (
                  <IconButton
                    className={classes.paper}
                    onClick={() => opengallery("g")}
                    //disabled={gallerydisabled}
                  >
                    <GalleryIcon style={{ fill: "#1a416a" }} />
                  </IconButton>
                )}
              </ListItem>
              <ListItem>{role === "agent" ? <ScreenRecording /> : ""}</ListItem>
            </List>
          </div>
        </Drawer>
      </Grid>
    </div>
  );
}
