import React, { useState } from "react";
import { Redirect, useHistory } from "react-router-dom";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import axios from "axios";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import IconButton from "@material-ui/core/IconButton";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import InputLabel from "@material-ui/core/InputLabel";
import InputAdornment from "@material-ui/core/InputAdornment";
import TextField from "@material-ui/core/TextField";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import Button from "@material-ui/core/Button";
import BgImg from "../assets/img/Login-bgImg.jpg";
import Logo from "../images/Logo.png";
//import Stepper from "../components/Stepper";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    height: "100%",
  },

  margin: {
    margin: theme.spacing(1),
  },
  withoutLabel: {
    marginTop: theme.spacing(3),
  },
  textField: {
    width: "25ch",
  },
  background: {
    backgroundImage: `URL(${BgImg})`,
    /* Full height */
    height: "100%",

    /* Center and scale the image nicely */
    backgroundPoisition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    alignContent: "center",
    alignSelf: "flex-start",
  },
  login: {
    marginTop: "100px",
    backgroundImage: `URL(${BgImg})`,
    color: "black",
    alignItems: "center",
    display: "flex",
    flexFlow: "column",
    width: "70%",
    marginLeft: "15%",
  },
  logo: {
    // background:"#1a416a",
    marginTop: "10px",
  },
}));

export default function LoginPage() {
  const history = useHistory();
  const [username, setUsername] = useState();
  const [password, setPassword] = useState();
  const [role, setRole] = useState();
  const [showPassword, setShowPassword] = useState(false);
  const classes = useStyles();
  const token = localStorage.getItem("token");
  let [loggedin, setLoggedin] = useState(token ? true : false);
  const [userInfo, setUserInfo] = useState();

  const formSubmit = async (event) => {
    event.preventDefault();
    const reqData = {
      username: username,
      password: password,
      role: role,
    };
    // console.log(reqData);

    //axios.defaults.headers.post["Access-Control-Allow-Origin"] = true;
    await axios
      //.post("http://localhost:5000/login", { reqData })
      .post("/login", { reqData })
      .then((resp) => {
        //console.log(resp.data);
        const token = resp.data.token;
        const user = resp.data.user;
        // console.log(user);
        localStorage.setItem("token", token);
        setLoggedin(true);
        setUserInfo(user);
        if(role === "user"){
          history.push("/stepper", userInfo)
        }else{
          history.push("/agent");
        }



      })
      .catch((error) => {
        console.log("axios error", error);
        if (error === "Error: Network Error") {
          alert("Something Went Wrong. Please Try Again!!");
        } else {
          console.log(error);
          alert("Invalid Credentails!!");
        }
        setPassword("");
        setShowPassword(false);
        setRole("");
        setUsername("");
      });
    //console.log(event);
  };
  console.log(loggedin, role);
  return (
    <div>
        <div className={classes.login}>
          <img src={Logo} alt="" className={classes.logo}></img>
          <h3>Login</h3>

          <TextField
            label="Username"
            id="outlined-start-adornment"
            value={username}
            className={clsx(classes.margin, classes.textField)}
            variant="outlined"
            onChange={(event) => setUsername(event.target.value)}
          />
          <br />

          <FormControl
            className={clsx(classes.margin, classes.textField)}
            variant="outlined"
          >
            <InputLabel htmlFor="outlined-adornment-password">
              Password
            </InputLabel>
            <OutlinedInput
              id="outlined-adornment-password"
              type={showPassword ? "text" : "password"}
              value={password}
              onChange={(event) => setPassword(event.target.value)}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowPassword(!showPassword)}
                    onMouseDown={(e) => e.preventDefault()}
                    edge="end"
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
              labelWidth={70}
            />
          </FormControl>
          <br />
          <FormControl component="fieldset">
            <RadioGroup
              aria-label="role"
              name="role"
              value={role}
              onChange={(e) => setRole(e.target.value)}
              style={{ display: "block" }}
            >
              <FormControlLabel value="user" control={<Radio />} label="User" />
              <FormControlLabel
                value="agent"
                control={<Radio />}
                label="Agent"
              />
            </RadioGroup>
          </FormControl>
          <br />
          <Button variant="contained" color="primary" onClick={formSubmit}>
            Login
          </Button>
        </div>
    </div>
  );
}
