import React from "react";
import { withStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import KYCData from "../KYCDetails.json";
import GridItem from "./Grid/GridItem";
import GridContainer from "./Grid/GridContainer";
import Button from "@material-ui/core/Button";
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import axios from "axios";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import "./KYCDetails.css";
import Fly from "../images/Fly.png";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Carousel from "react-images";
import { photos } from "../photos";
import "./Gallery.scss"

const styles = theme => ({
  kycContent: {
         width: "213px",
         height: "51px",
         fontFamily: "Poppins",
         fontSize: "36px",
         fontWeight: "bold",
         fontStretch: "normal",
         fontStyle: "normal",
         lineHeight: "1.39",
         letterSpacing: "normal",
         textAlign: "center",
         color: "#1a416a",
       },
       root: {
             "&.makeStyles-grid-22": {
               padding: "0px 0px 300px 200px",
             },
           },
});

  
export class KYCDetailsGallery extends React.Component {
   
  constructor(props) {
    super(props);
    this.state = {
      imgChk:false,
      tmpVar:0,
      photoData:props.imageArray,
      //photoDataInc:photos,
      documents:"0",
      //queIndex:0, 
      verData:KYCData ,
      finData:[{"documentType":"self","img":"",geolocation:"Noida",requestNumber:"5f8d6887a5d350251fc0d491" }] 
    };
    this.state.verData.map((data, key) => (
      this.state.finData.push({"documentType":data.documentType,"img":"",geolocation:"Noida",requestNumber:"5f8d6887a5d350251fc0d491"})
    ))
  
  }

  componentDidMount() {
    axios
    .get(
      "http://18.191.100.2:9092/kyccustomerservice/questionAndAnswer/5f8d6887a5d350251fc0d491"
    )
    .then((res) => {
      console.log("res", res);
     // setTemp(1);
     this.setState({
      verData:res.data
    });
     
      
    })
    .catch((error) => {
      console.log(error);
      alert("Error 404");
    });
  }
  
   
handleCheckbox = () => {
    if(this.state.tmpVar ===0)
    {
    this.props.getChkboxVal( !this.state.imgChk);
    this.setState({
      tmpVar:1
    });
    }
    else{
      this.props.getChkboxVal( this.state.imgChk?"xyz":"abc");
      this.setState({
        tmpVar:0
      });
    }
    this.setState({
      imgChk:!this.state.imgChk
    });
   // const modifiedFindata = {...,img:"documents"}
    let modifiedFinData = this.state.finData;
    modifiedFinData[this.state.documents] = {...this.state.finData[this.state.documents],img:this.state.photoData[this.props.imgIndex].src}
    this.setState({
      finData:modifiedFinData
    });
    console.log("finData",this.state.finData);
  };

  handleSubmit = () => {

    const fdata = []
    console.log(this.state.finData);
  this.state.finData.map((data, key) => {
    if(data.img !== "")
    {
     
     fdata.push(this.state.finData[key])
           
    }
  })

    console.log("fdata",fdata);
   
      axios
        //.post("http://localhost:8080/kycphotoservice/savephotoobject", this.state.finData)
        .post("http://18.191.100.2:9091/kycphotoservice/savephotoobject", fdata)
        .then((res) => {
         console.log("res", res);
  
          alert("Data Saved Sucessfully");
        })
        .catch((error) => {
          console.log(error);
          alert("Error 404");
        });
    };
handleButtons = (e, str) => {
  this.setState({
    queIndex :0
  });
   
    if (str !== 0) {
      document.getElementById("onSubmit").disabled = false;
      
    } else {
     
      document.getElementById("onSubmit").disabled = true;
    }
    this.setState({
      documents:  str

    });
   
  };

onSubmit = (event) => {
   // console.log(event);
  };

  componentWillReceiveProps(props) 
  { 
    // console.log("receivedprops", props)
    // console.log("receivedstate", this.state)
    if(props.indChkVal !== this.state.imgChk)
     {this.setState({
      imgChk:!this.state.imgChk
    });
   // this.props.getChkboxVal( !this.state.imgChk);
     }
    
    //  console.log("this.state.photoData.length", this.state.photoData.length)
      if (props.imageArray.length  !== this.state.photoData.length)
      {
       props.imageArray.map((tile,key) => {
         if(key>this.state.photoData.length-1)
         {
           const pht = this.state.photoData;
           pht.push(props.imageArray[key])
           this.setState({
             photoData:pht
           });
         
      }
       });

     }
    //  console.log("props.imageArray", props.imageArray)
    //  console.log("this.statephotodataIN", this.state.photoData)
    };
    render() {
    const { classes } = this.props;
  
  return (  
      <div>
      <GridContainer>
        <GridItem className="detail-container">
        <GridItem xs={12} sm={12} md={12} >
          <p className="KYC-Details">KYC Details</p>
        <FormControl variant="outlined" className={classes.formControl}>
        <div className="my-dd"><InputLabel id="demo-simple-select-outlined-label">
              Documents
            </InputLabel>
            <Select
              // style = {{width: 200}}
              style={{
                width: 299,
                height: "54px",
                backgroundColor: "#ffffff",
                borderRadius: "5px",
                border: "1px #b2b2b2",
                textAlign: "left",
              }}
              
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              value={this.state.documents}
              onChange={(e, str) => this.handleButtons(e, e.target.value)}
              label="documents"
            >
              <MenuItem value="0">
                <em> None </em>
              </MenuItem>
              {this.state.verData.map((data, key) => (
                <MenuItem value={key + 1} key={key + 1}>
                  {" "}
                  {data.documentType}
                </MenuItem>
              ))}
              {/* {verData?v.erData.map((data,key) => (
                    <MenuItem value={key+1}> {data.documentType}</MenuItem>
                )):""} */}
            </Select>
            </div> </FormControl>

         
<div className="image-container">
         <GridContainer>
            <input type="checkbox" onClick={this.handleCheckbox} checked={this.state.imgChk} className="custom-control-input" id="ck1a"/>
              {/* <Carousel
              currentIndex={this.props.imgIndex}
              views={photos.map(x => ({ustom-con
                 ...x })
              )} 
                         
            />  */}
            <div className="image-kyc-detail">
            {/* {console.log("this.state.inrender",this.state)}
              {console.log("this.state.photoData",this.state.photoData)} */}

            <img src={this.state.photoData[this.props.imgIndex].src} className="img-fluid" alt={this.state.photoData[this.props.imgIndex].height} />
            </div>
          </GridContainer>
          </div>
          <GridContainer>
            <GridItem>
              <Button
                variant="contained"
                color="primary"
                size="small"
                className="Submit"
                // startIcon={<SaveIcon />}
                //disabled={!trvChk}
                type="submit"
                id="onSubmit"
                onClick={this.handleSubmit}
              >
                SUBMIT<img src={Fly} className="send" alt="Submit"></img>
              </Button>
            </GridItem>
          </GridContainer>
        </GridItem>
        </GridItem>
      </GridContainer>
    </div>
  );
  }
}
export default withStyles(styles)(KYCDetailsGallery);