import React, { useState } from "react";
import Grid from "@material-ui/core/Grid";
import CommonDrawer from "./CommonDrawer";
import Callinglayouttoolbar from "./Callinglayouttoolbar";
import KYCDetails from "./KYCDetails";
import GalleryFrame from "./GalleryFrame";
import KYCDetailsGallery from "./KYCDetailsGallery";
import VideoCalling from "./VideoCall/VideoCalling";
import WaitingScreen from "./WaitingScreen/WaitingScreen";

export default function MainCallinglayout(props) {
  //console.log("readyParams:", props);
  let role = "agent";
  if (props.location.state) {
    role = props.location.state.loginRole;
    console.log("role",role);
  }
console.log("props.loginRole",props);
  const [queStt, setQueStt] = useState("");
  const [galReqd, setGalReqd] = useState(false);
  const [galDis, setGalDis] = useState(true);
  const [vidReqd, setVidReqd] = useState(role==="agent"?true:false);
  const [imgIndex, setImgIndex] = useState(0);
  const [imgChkVal, setImgChkVal] = useState("");
  const [indChkVal, setIndChkVal] = useState(false);
  const [captureReqd, setCaptureReqd] = useState(0);
  const [callWindowOn, setCallWindowOn] = useState(false);
  const [imageArray, setImageArray]= useState("");
  let capture=0;
  const latitude = useState(
    props.location.state ? props.location.state.lat : ""
  );
  const longitude = useState(
    props.location.state ? props.location.state.long : ""
  );
  const setInd = (index, indChkVal) => {
    console.log("queLine", index);
    setImgIndex(index);

    setIndChkVal(indChkVal);
  };
  const setGalDisabled = (Val) => {
    setGalDis(Val);
  };
  const chkHandle = (chkboxVal) => {
    console.log("chkHandle with ", imgIndex, "  -", chkboxVal);
    setImgChkVal(chkboxVal);
  };

  const setQue = (queLine) => {
    console.log("queLine", queLine);
    setQueStt(queLine);
  };
  const chkCallWindowStatus = (status) => {
    if (status === "active") setCallWindowOn(true);
    console.log("callWindow:", callWindowOn);
  };
  const setGalVal = (galReqd) => {
    setGalReqd(galReqd);
  };
  const setVidVal = (vidReqd) => {
    setVidReqd(vidReqd);
  };

  const onPhotoCaptureClick = (captureReqd) => {
    capture= capture+captureReqd
    console.log('aaloo',capture);
    setCaptureReqd(capture);
  };
  const layoutParam = {
    latitude: latitude,
    longitude: longitude,
    loginRole: role,
  };
function setimagearrayparent (arr){
    console.log("arr",arr);
    setImageArray(arr);
  };

  return (
    <div className="side-menu">
      <Grid container >

        {/* <Grid item xs='1'> */}
        <CommonDrawer
          galreqd={setGalVal}
          vidreqd={setVidVal}
          galDis={galDis}
          captureReqd={onPhotoCaptureClick}
          loginRole={role}
        />
        {/* </Grid> */}
        {!vidReqd ? (
          <>
            <Grid item xs={12} style={{ padding: "0" }}>
              <Callinglayouttoolbar layoutparam={layoutParam} role={role} />
            </Grid>
            <Grid item xs={12}>
              <WaitingScreen role={role}/>
            </Grid>
          </>
        ) : (
          <>
            <Grid item xs={8}>
              <Grid container>
                <Grid item xs={12} style={{ padding: "0" }}>
                  <Callinglayouttoolbar layoutparam={layoutParam} />
                </Grid>
                <Grid item xs={12}>
                 
                  {vidReqd && (
                    <VideoCalling clickImage={captureReqd} role={role}  setimagearray = {setimagearrayparent}  />
                  )}                </Grid>
                <Grid container alignItems="flex-end">
                  <Grid item xs={12}>
                    <div className="Dummy-1"></div>
                    <div className="Rectangle-7">
                      {galReqd? (
                        <GalleryFrame setIndex={setInd} imgChkVal={imgChkVal} imageArray={imageArray} />
                      ) : (
                        <div className="Question">{queStt}</div>
                      )}
                    </div>
                  </Grid>
                
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={4}>
              {galReqd? (
                <KYCDetailsGallery
                  imgIndex={imgIndex}
                  getChkboxVal={chkHandle}
                  indChkVal={indChkVal}
                  imageArray={imageArray}
                />
              ) : (
                <KYCDetails parentCallback={setQue} setGalDis={setGalDisabled}></KYCDetails>
              )}
            </Grid>
          </>
        )}

        {/* <Grid item xs='8'>
                <div className="Dummy-1">
                    </div> 
                    </Grid>    */}
      </Grid>
    </div>
  );
}
