import React,{useState} from 'react'
import GridItem from "./Grid/GridItem.js";
import GridContainer from "./Grid/GridContainer.js";
import Card from "./Card/Card";
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { makeStyles ,withStyles} from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import CaptureDialogbox from './CaptureDialogbox';
import  InstructionModal  from './InstructionModal';
import UploadDialogbox from './UploadDialogbox'
import axios from "axios";
import MaterialTable from 'material-table';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import SortArrow from '@material-ui/icons/Sort';
import ViewColumn from '@material-ui/icons/ViewColumn';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import EditIcon from '@material-ui/icons/Edit';
import CloseIcon from '@material-ui/icons/Close';
import AddIcon from '@material-ui/icons/Add';
import './Style.css';
  
const useStyles = makeStyles((theme) => ({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
    input: {
      display: 'none',
    },
    root: {
      '& > *': {
        margin: theme.spacing(1),
      },
    },
    root1: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
    
  }));

  
  function UploadDocumentScreen() {
    const classes = useStyles();
  

    const [data,setData] = useState('');
    const [Doctype,setDoctype] = useState('');
    const [Doctypename,setDoctypename] = useState('');
    const [docNum,setDocNum] = useState('');
    const[uploadedfile, setUploadedfile] = useState(true);
    const [loc,setLoc] = useState('');
    const [mandFill,setMandFill] = useState(false);
   
   
    
    function handlefileChange(newValue) {
      setUploadedfile(newValue);

    

     const location = "Long:"+loc.lon+" Lat:"+ loc.lat;
     console.log('location',location);
     const webBrow = window.chrome?'Chrome':'OtherThan Chrome'
      if(data === "")
    {
      
    const tabledata ={
      columns: [
        { title: '  Document Type', field: 'Doctypename' },
        { title: 'Document Number', field: 'docNum' },
        { title: 'File Name', field: 'Fname'},
        {
          title: 'Location',
          field: 'loc',
          
        },
        
      ],
      data: [
        
        { reqid:'5f8d6887a5d350251fc0d491',Doctypename: Doctypename, docNum: docNum,file:newValue.file?newValue.file:newValue, Fname: newValue.file?newValue.file.name:"<<capturedImage.jpg>>", loc: 'Ghaziabad', Wbrowser: webBrow},
       
        
      
      ],
     

    }
  
    setData(tabledata);
    setDocNum("")
    setDoctype("")
    setDoctypename("")
    setMandFill(false)
   
  }
  else{
    
    setData((prevState) => {
        const data = [...prevState.data];
        data.push({ reqid:'5f8d6887a5d350251fc0d491',Doctypename: Doctypename, docNum: docNum,file:newValue.file?newValue.file:newValue, Fname: newValue.file?newValue.file.name:"<<capturedImage.jpg>>", loc: 'Noida',Wbrowser: webBrow  });
        return { ...prevState, data };
      });

    
    setDocNum("")
    setDoctype("")
    setDoctypename("")
    setMandFill(false)
  }
  
    }
    const handleDocNum= (newValue) => {
    if(Doctype==="")
{alert("Please select Document Type")
  return}
  else if(Doctype === 4)
  {
    alert("No need to input document number, capture the image")
    return
  }
      setDocNum(newValue);
      setMandFill(true);
    }
   

  const handleChange = (event,nm) => {
    setDoctype(event.target.value);
    navigator.geolocation.getCurrentPosition(getPosition);
    setDoctypename(nm.props.name)
    setDocNum("");
      setMandFill(false);
    if(event.target.value === 4)
    {
     //we have to disable text box and upload button
     setUploadedfile(false) 
     setMandFill(true)

    }
else {setUploadedfile(true) 
  setMandFill(false)
  setUploadedfile(true) 
}

  };

  function getPosition(position) {
    
    const loc = {lat:position.coords.latitude, lon:position.coords.longitude}
    
    setLoc(loc)
    
  }
  
  const handleSave = (event) => {
    const formData = new FormData();
   if(!data.data ||data.data.length===0  )
   {
     alert('Please add one record atleast')
     return
   }
    for(let i = 0; i< data.data.length; i++) {
     
    formData.append('requestID', data.data[i].reqid)
    formData.append('documentType', data.data[i].Doctypename)
    formData.append('documentNumber', data.data[i].docNum)
    formData.append('kycLocation', data.data[i].location)
    formData.append('browser', data.data[i].Wbrowser)
    formData.append('kycDocImage', data.data[i].file)
    console.log('for',i)
  }
  
//axios.post('http://localhost:8080/kycuploadservice/uploadDocuments', formData)
axios.post('http://18.191.100.2:9091/kycuploadservice/uploadDocuments', formData)
    .then(res=>{ console.log("res",res)
   
    alert('Data Saved Sucessfully')
  setData('')
})

.catch(error => {  console.log(error) 
  alert("Error 404")
})
};

  
      return (
        <div className='upload-wrap'>
          <GridContainer>
           <GridItem xs={12} sm={12} md={12}>
        
      </GridItem>
      </GridContainer>
     <GridContainer>
         <GridItem xs={12} sm={12} md={12}>
           
           
     <GridContainer>
     <GridItem xs={12} sm={12} md={12} >
       
     </GridItem>
     <GridItem xs={12} sm={12} md={12}>
     <GridItem xs={12} sm={12} md={12}>
       <div>
         <h2 className="secondary-heading">You can upload multiple document here<span className="my-instruction"> <InstructionModal/></span></h2>
         
       
        </div>
       
       <GridItem xs={12} sm={12} md={8}>
      
         </GridItem>
       </GridItem>

       
       </GridItem>
       <GridItem xs={12} sm={12} md={12} style={{marginLeft:10,marginRight:10}}>
         <Card>
               <GridContainer>
               <GridItem xs={12} sm={12} md={4}>
               <FormControl variant="outlined" className={classes.formControl} style={{ width: "350px",marginTop:'12px' }}>
        <InputLabel id="demo-simple-select-outlined-label">Select Document Type Here</InputLabel>
                   {/* <FormControl className={classes.formControl} style={{ width: "350px" }}> */}
                            
     {/* <InputLabel id="demo-simple-select-readonly-label">Select Document Type Here</InputLabel> */}
     <Select
         labelId="demo-simple-select-outlined-label"
         id="demo-simple-select-outlined"
         label="Select Document Type Here"
      value={Doctype}
      name={Doctypename}
      variant="outlined"
      onChange={ (e, name)=>handleChange(e,name)}
       inputProps={{ readOnly: false }}
     >
      
       <MenuItem value={1} name='Pan Card'>Pan Card</MenuItem>
       <MenuItem value={2} name='Aadhar Card'>Aadhar Card</MenuItem>
       <MenuItem value={3} name='Driving license'>Driving license</MenuItem>
       <MenuItem value={4} name='Customer Image'>Customer Image</MenuItem>
     </Select>
     
     {/* <FormHelperText>Select Document Type Here</FormHelperText> */}
   </FormControl>
  </GridItem>
  <GridItem xs={12} sm={12} md={4}>
  <TextField
          required       
          label="Enter Document Number Here"       
          variant="outlined"
          id="validation-outlined-input"
          value={docNum}
     style={{ width: "350px",marginTop:15 }}
     onChange={e => handleDocNum(e.target.value)}
        />
   
   </GridItem>
  
   <GridItem xs={12} sm={12} md={4}>
   
  
   <div className={classes.root}>
   <GridContainer>
   <GridItem xs={12} sm={12} md={3}>
      <UploadDialogbox uploadedfile={uploadedfile} onChange={handlefileChange} mandFill={mandFill}></UploadDialogbox>
      </GridItem>
     
      <GridItem xs={12} sm={12} md={3}>
      <CaptureDialogbox uploadedfile={uploadedfile} onChange={handlefileChange} mandFill={mandFill} Doctype={Doctype}> 
     </CaptureDialogbox>
      </GridItem>
      <GridItem xs={12} sm={12} md={3}>
      <Button
        variant="contained"
        style={{background:'#1a416a',color:'white',marginTop:'25px',marginLeft:'45px'}}
        size="large"
        onClick={handleSave}
        startIcon={<SaveIcon />}
        color='primary'
      >
        SAVE
      </Button>
      </GridItem>
              </GridContainer>
              </div>
              
   </GridItem>
   </GridContainer>
      
             </Card>
       </GridItem>
       
     </GridContainer>
         </GridItem>
  </GridContainer>
  <div align='right' style={{marginRight:10}}>
  
  
      <span style={{whiteSpace:'pre-line'}}>{'\t'}</span>
     	
      </div>
     <br></br>
      {data?
      <MaterialTable
      options={{
        search: false,
      }}
    icons={{
        Check: () => <Check /> ,
        DetailPanel: () => <ChevronRight /> ,
        Export: () => <SaveAlt  /> ,
        Filter: () => <FilterList /> ,
        FirstPage: () => <FirstPage /> ,
        LastPage: () => <LastPage /> ,
        NextPage: () => <ChevronRight /> ,
        PreviousPage: () => <ChevronLeft />,
        Search: () => <Search />  ,
        SortArrow: () => <SortArrow /> ,
        ThirdStateCheck: () => <Remove /> ,
        ViewColumn: () => <ViewColumn /> ,
        Delete:()=><DeleteOutlineIcon/>,
        Edit:()=><EditIcon/>,
        Clear:()=><CloseIcon/>,
        Add:()=><AddIcon/>,
        ClearSearch:()=><CloseIcon/>
      }}
        
      title="Upload Document Details"
      columns={data.columns}
      data={data.data}
      isFreeAction={true}
      
      editable={{
      
        onRowDelete: (oldData) =>
          new Promise((resolve) => {
            setTimeout(() => {
              resolve();
              setData((prevState) => {
                const data = [...prevState.data];
                data.splice(data.indexOf(oldData), 1);
                return { ...prevState, data };
              });
            }, 600);
          }),
      }}
    />:""}
</div>
  
      )
  }
  export default UploadDocumentScreen
  
  