import React, { useEffect, useState } from 'react'
import axios from 'axios'
const publicIp = require('public-ip');


function VPNdetection({ parentCallback }) {

const   [publicIP1,setpublicIP1]=useState('');
const [fetched, setFetched] = useState(false);
const   [responseIPScore,setresponseIPScore]=useState(''); 

      useEffect(() => {
        if (fetched === false) {
            getIPScore();
           //axios.get('http://check.getipintel.net/check.php?ip='+publicIP1+'&contact=A@GMAIL.COM')
            axios({
              method: "get",
              //headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
             // url: 'http://check.getipintel.net/check.php?ip='+publicIP1+'&contact=A@GMAIL.COM',
              //withCredentials: true,
               headers: {
                "Access-Control-Allow-Origin": "*",
                 'Content-Type': 'application/x-www-form-urlencoded' ,
              //   Accept: "application/json"
               }
            })
            .then(res => {
                if (res) {
                    console.log('res',res.data);
                    setresponseIPScore(res.data)
                } else { 
                }
              })
              .catch(error => {
                console.log(error);
               
              });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
      }, [fetched,publicIP1]);
      
      const getIPScore = async () => {
        try { 
          const [ publicIP] = await Promise.all([publicIp.v4()]);
            setpublicIP1(publicIP)       
          setFetched(true);
        } catch (err) {
          console.log(err);
        }
      };

      parentCallback(responseIPScore);
//console.log('responseIPScore',responseIPScore)

    return (
        <div>
            {/* <h1><font color='green'>{responseIPScore}</font></h1> */}
        </div>
    )
}

export default VPNdetection